body {
    background: #252525;
    font-family: Verdana, sans-serif;
    font-size: 12px;
    color: #dedede;
}

a {
    color: #15cbd1;
}

#table table {
    border-spacing: 0;
    // border-collapse: collapse;
    background: #000;
}

#table td {
    border: solid 1px black;
    border-color: #fff #000 #000 #fff;
    width: 15px;
    height: 15px;
    text-align: center;
}

#table td.color-0 {
    background: transparent;
    border-color: transparent;
}

#table td.color-1 {
    background: cyan;
}

#table td.color-2 {
    background: orange;
}

#table td.color-3 {
    background: blue;
}

#table td.color-4 {
    background: red;
}

#table td.color-5 {
    background: green;
}

#table td.color-6 {
    background: purple;
}

#table td.color-7 {
    background: yellow;
}

.cell-row-0,
.cell-row-1,
.cell-row-2,
.cell-row-3 {
    display: none;
}


